import React, { useState, useEffect } from "react";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Row,
  CardFooter,
  Button,
  Input,
  FormGroup,
  Label,
} from "reactstrap";
import "react-toastify/dist/ReactToastify.css";
import { toast, ToastContainer } from "react-toastify";
import axios from "axios";
import Spinner from "reactstrap/lib/Spinner";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import Classes from "./product.module.css";
import { fileType, maxFileSize, selectMaxFile } from "../Media/Funcs";

const Addproduct = (props) => {
  const [values, setValues] = useState({
    fname: "",
    ename: "",
  });
  const [status, setStatus] = useState(false);
  const [Maincategory, setMaincategory] = useState("");
  const [Subcategory, setSubcategory] = useState("");
  const [SubSubcategory, setSubSubcategory] = useState("");
  const [sellerID, setSeller] = useState("");
  const [brand, setBrand] = useState("");
  const [waranty, setWaranty] = useState("");
  const [color, setColor] = useState("");
  const [price, setPrice] = useState("");
  const [discontPrice, setDiscontPrice] = useState("");
  const [stock, setStock] = useState("");
  const [description, setDescription] = useState("");
  const [image, setImage] = useState("");
  const [file, setFile] = useState("");

  const [allMainCategoryServerSide, setAllMainCategoryServerSide] = useState(
    []
  );
  const [allSubCategoryServerSide, setAllSubCategoryServerSide] = useState([]);
  const [allSubSubCategoryServerSide, setAllSubSubCategoryServerSide] =
    useState([]);
  const [allBrandFromServer, setAllBrandFromServer] = useState([]);
  const [allSeller, setAllSeller] = useState([]);
  const [allWaranty, setAllWaranty] = useState([]);
  const [allAttribute, setAllAttribute] = useState([]);
  const [allSpecs, setAllSpecs] = useState([]);
  const [result, setResult] = useState([]);

  const [CatId, setCatId] = useState(null);
  const [mainCategoryBoolean, setMainCategoryBoolean] = useState(false);
  const [subCategoryBoolean, setSubCategoryBoolean] = useState(false);
  const [subsubCategoryBoolean, setSubSubCategoryBoolean] = useState(false);
  const [SubmitLoading, setSubmitLoading] = useState(false);

  useEffect(() => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllCategory($page:Int,$limit:Int,$mainCategory:Boolean,$hasParent:Boolean,$catId:ID) {
          getAllCategory(input:{page:$page,limit:$limit,mainCategory:$mainCategory,hasParent:$hasParent,catId:$catId}){
            _id,
            name,
            label,
            parent{
              name
            }
          }
        }`,
        variables: {
          page: 1,
          limit: 30,
          mainCategory: false,
          hasParent: false,
          catId: null,
        },
      },
    })
      .then((res) => {
        //all category
        const { getAllCategory } = res.data.data;
        setResult(getAllCategory);

        //category parent null
        let arr = [];
        for (let index = 0; index < getAllCategory.length; index++) {
          const element = getAllCategory[index];
          if (element.parent == null) {
            arr.push(element);
          }
        }
        setAllMainCategoryServerSide(arr);
      })
      .catch((err) => console.log(err));

    //get all waranty
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
            query allWaranty($page:Int,$limit:Int){
              getAllWaranty(page:$page,limit:$limit){
                _id,
                name,
                label
              }
            }
                `,
        variables: {
          page: 1,
          limit: 30,
        },
      },
    })
      .then((res) => {
        if (res.data.errors) {
          const { message } = res.data.errors[0].data[0];
          toast.error(message);
        } else {
          const { getAllWaranty } = res.data.data;
          setAllWaranty(getAllWaranty);
        }
      })
      .catch((err) => console.log(err));
  }, []);

  useEffect(() => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllCategory($page:Int,$limit:Int,$mainCategory:Boolean,$hasParent:Boolean,$catId:ID) {
          getAllCategory(input:{page:$page,limit:$limit,mainCategory:$mainCategory,hasParent:$hasParent,catId:$catId}){
            _id,
            name,
            label,
            parent{
              name
            }
          }
        }`,
        variables: {
          page: 1,
          limit: 30,
          mainCategory: false,
          hasParent: true,
          catId: CatId,
        },
      },
    })
      .then((res) => {
        const { getAllCategory } = res.data.data;
        setAllSubCategoryServerSide(getAllCategory);
      })
      .catch((err) => console.log(err));
  }, [status, mainCategoryBoolean, subCategoryBoolean, CatId]);

  const handleState = (e) => {
    const { name, value } = e.target;
    setValues((prev) => ({ ...prev, [name]: value }));
  };

  const handleStateMain = (e) => {
    setMaincategory(e.target.value);
    setMainCategoryBoolean(true);
    setCatId(e.target.value);

    axios({
      url: "/",
      method: "post",
      data: {
        query: `
          query getAllSeller($catId:ID!){
              getAllSellers(catId:$catId){
                _id,
                name,
                label,
              }
            }
              `,
        variables: {
          catId: e.target.value,
        },
      },
    })
      .then((res) => {
        const { getAllSellers } = res.data.data;
        setAllSeller(getAllSellers);
      })
      .catch((err) => console.log(err));
  };

  const handleStateSub = (e) => {
    setSubcategory(e.target.value);
    setMainCategoryBoolean(false);
    setSubCategoryBoolean(true);

    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query GetSpecsAndChild($subCatId:ID){
          GetSpecsAndChild(subCatId:$subCatId){
           specs{
             _id,
            name,
            subSpec{
              _id,
              name
            }
          }
          }
        }`,
        variables: {
          subCatId: e.target.value,
        },
      },
    })
      .then((res) => {
        const { specs } = res.data.data.GetSpecsAndChild;
        setAllSpecs(specs);
      })
      .catch((err) => console.log(err));
  };

  const handleChangeBrand = (e) => {
    setBrand(e.target.value);
  };

  useEffect(() => {
    //get all subsubcategory
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllCategory($page:Int,$limit:Int,$mainCategory:Boolean,$hasParent:Boolean,$catId:ID) {
          getAllCategory(input:{page:$page,limit:$limit,mainCategory:$mainCategory,hasParent:$hasParent,catId:$catId}){
            _id,
            name,
            label,
            parent{
              name
            }
          }
        }`,
        variables: {
          page: 1,
          limit: 30,
          mainCategory: false,
          hasParent: true,
          catId: Subcategory,
        },
      },
    })
      .then((res) => {
        const { getAllCategory } = res.data.data;
        setAllSubSubCategoryServerSide(getAllCategory);
      })
      .catch((err) => console.log(err));

    //all brands
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
          query getAllBrand($page:Int,$limit:Int,$getAll:Boolean,$catId:ID){
            getAllBrand(input:{page:$page,limit:$limit,getAll:$getAll,catId:$catId}){
              _id,
              name,
              label,
              category{
                _id,
                name
              },
            }
          }`,
        variables: {
          page: 1,
          limit: 30,
          getAll: false,
          catId: Subcategory,
        },
      },
    })
      .then((res) => {
        const { getAllBrand } = res.data.data;
        setAllBrandFromServer(getAllBrand);
      })
      .catch((err) => console.log(err));
  }, [Subcategory]);

  const handleStateSubSub = (e) => {
    setSubSubcategory(e.target.value);
    setSubSubCategoryBoolean(true);
  };

  const handleStateSeller = (e) => {
    setSeller(e.target.value);
  };

  const handleChangeWaranty = (e) => {
    setWaranty(e.target.value);
  };
  const handleChangeColor = (e) => {
    setColor(e.target.value);
  };
  const handleChangePrice = (e) => {
    setPrice(e.target.value);
  };
  const handleChangeAmount = (e) => {
    setStock(e.target.value);
  };
  const handleChangeDiscountPrice = (e) => {
    setDiscontPrice(e.target.value);
  };

  const convertIdtoNameSeller = (id) => {
    const SellerItem = allSeller.filter((seller) => seller._id === id);
    return SellerItem[0].name;
  };

  const convertIdtoNameWaranty = (id) => {
    if (!id) {
      return null;
    }
    const WarantyItem = allWaranty.filter((waranty) => waranty._id === id);
    return WarantyItem[0].name;
  };

  const addAttribute = (e) => {
    if (!sellerID || !waranty) {
      toast.warn("مقدار فروشنده یا گارانتی را ثبت کنید");
      return false;
    }

    setAllAttribute((prev) => [
      ...prev,
      {
        seller: sellerID,
        waranty: waranty,
        stock: parseInt(stock),
        color: color,
        price: parseInt(price),
        discount: parseInt(discontPrice),
      },
    ]);
    setDiscontPrice("");
    setStock("");
    setPrice("");
    setColor("");
  };

  const removeAttribute = (id) => {
    const copy = [...allAttribute];
    copy.splice(id, 1);
    setAllAttribute(copy);
  };

  const handleValueSpecs = (e, SpecID, id) => {
    const copy = [...allSpecs];
    const valueSpecs = copy[SpecID].subSpec[id];
    valueSpecs.value = e.target.value;
    setAllSpecs(copy);
  };

  const handleValueSpecsLabel = (e, SpecID, id) => {
    const copy = [...allSpecs];
    const valueSpecs = copy[SpecID].subSpec[id];
    valueSpecs.label = e.target.value;
    setAllSpecs(copy);
  };

  const descriptionHandler = (event, editor) => {
    const data = editor.getData();
    setDescription(data);
  };

  const handlerImage = (event) => {
    if (fileType(event, false) && maxFileSize(event, false)) {
      setFile(event.target.files[0]);
      const privew = URL.createObjectURL(event.target.files[0]);
      setImage(privew);
    }
  };

  const handleSubmitUplaod = () => {
    if (!values.fname || !values.ename) {
      toast.error("مقدار نام محصول خالی می باشد");
      return false;
    }
    if (!description) {
      toast.error("مقدار توضیحات محصول خالی می باشد");
      return false;
    }
    if (!file) {
      toast.error("مقدار تصویر محصول خالی می باشد");
      return false;
    }

    setSubmitLoading(true);
    let IDforserver = null;
    if (subsubCategoryBoolean) {
      IDforserver = SubSubcategory;
    } else {
      IDforserver = Subcategory;
    }

    // //check emoty value specs
    // if (
    //   allSpecs.map((value) => value.subSpec.map((item) => item.value == ""))
    // ) {
    //   toast.warn("فیلد مشخصات باید مقدار دهی شود");
    //   return false;
    // }

    //valuse Specs array create
    const ArrayValuseSpecs = [];
    allSpecs.map((value) =>
      value.subSpec.map((item) =>
        ArrayValuseSpecs.push({
          howspecs: item._id,
          value: item.value,
          label: item.label,
        })
      )
    );

    console.log(brand);
    let data = {
      query: `
      mutation addProduct($fname:String!,$ename:String!,$category:ID!,$brand:ID!,$description:String!,$valuespecs:[inputValueSpecs!]!,$attribute:[InputAttribute!]!,$orginal:Upload!){
        product(input:{fname:$fname,ename:$ename,category:$category,brand:$brand,description:$description,valuespecs:$valuespecs,attribute:$attribute,orginal:$orginal}){
          status,
          message
        }
      }`,
      variables: {
        fname: values.fname,
        ename: values.ename,
        brand: brand,
        category: IDforserver,
        description: description,
        valuespecs: ArrayValuseSpecs,
        attribute: allAttribute,
        orginal: null,
      },
    };
    let map = {
      0: ["variables.orginal"],
    };
    const formD = new FormData();
    formD.append("operations", JSON.stringify(data));
    formD.append("map", JSON.stringify(map));
    formD.append(0, file, file.name);

    axios({
      url: "/",
      method: "post",
      data: formD,
    })
      .then((res) => {
        setSubmitLoading(false);
        const { status, message } = res.data.data.product;
        if (status === 200) {
          toast.success(message);
        } else {
          toast.error(message);
        }
      })
      .catch((err) => console.log(err));
  };

  return (
    <div className="animated fadeIn">
      <ToastContainer />
      <Row>
        <Col xs="12">
          <Card>
            <CardHeader>اضافه کردن محصولات</CardHeader>
            <CardBody>
              <FormGroup row>
                <Col xs="10">
                  <FormGroup>
                    <Label htmlFor="title">نام فارسی محصول</Label>
                    <Input
                      type="text"
                      id="title"
                      placeholder="نام فارسی محصول را وارد نمائید"
                      name="fname"
                      value={values.fname}
                      onChange={handleState}
                      required
                    />
                  </FormGroup>
                </Col>
                <Col xs="10">
                  <FormGroup>
                    <Label htmlFor="label">نام انگلیسی محصول</Label>
                    <Input
                      type="text"
                      id="label"
                      placeholder="نام انگلیسی محصول را وارد نمائید"
                      value={values.ename}
                      onChange={handleState}
                      name="ename"
                      required
                    />
                  </FormGroup>
                </Col>
                <Col xs="6" md="3">
                  <FormGroup>
                    <Label htmlFor="Maincategory">دسته اصلی</Label>
                    <Input
                      type="select"
                      name="Maincategory"
                      id="Maincategory"
                      value={Maincategory}
                      onChange={handleStateMain}
                    >
                      <option></option>
                      {allMainCategoryServerSide.map((item) => (
                        <option value={item._id} key={item._id}>
                          {item.name}
                        </option>
                      ))}
                    </Input>
                  </FormGroup>
                </Col>
                <Col xs="6" md="3">
                  <FormGroup>
                    <Label htmlFor="subMaincategory">زیر دسته</Label>
                    <Input
                      type="select"
                      name="Subcategory"
                      id="Subcategory"
                      value={Subcategory}
                      onChange={handleStateSub}
                    >
                      <option></option>
                      {allSubCategoryServerSide.map((item, index) => (
                        <option value={item._id} key={index}>
                          {item.name}
                        </option>
                      ))}
                    </Input>
                  </FormGroup>
                </Col>

                <Col xs="6" md="3">
                  <FormGroup>
                    <Label htmlFor="subMaincategory">زیر دسته دوم</Label>
                    <Input
                      type="select"
                      name="SubSubcategory"
                      id="SubSubcategory"
                      value={SubSubcategory}
                      onChange={handleStateSubSub}
                    >
                      <option></option>
                      {allSubSubCategoryServerSide
                        ? allSubSubCategoryServerSide.map((item, index) => (
                            <option value={item._id} key={index}>
                              {item.name}
                            </option>
                          ))
                        : null}
                    </Input>
                  </FormGroup>
                </Col>

                <Col xs="6" md="3">
                  <FormGroup>
                    <Label htmlFor="subMaincategory">برند</Label>
                    <Input
                      type="select"
                      name="brand"
                      id="brand"
                      value={brand}
                      onChange={handleChangeBrand}
                    >
                      <option></option>
                      {allBrandFromServer.map((item, index) => {
                        return (
                          <option key={index} value={item._id}>
                            {item.name}
                          </option>
                        );
                      })}
                    </Input>
                  </FormGroup>
                </Col>
                <hr
                  style={{ width: "100%", height: "3px", color: "black" }}
                ></hr>
                <Col xs="6" md="3">
                  <FormGroup>
                    <Label htmlFor="sellerID">فروشنده</Label>
                    <Input
                      type="select"
                      name="sellerID"
                      id="sellerID"
                      value={sellerID}
                      onChange={handleStateSeller}
                    >
                      <option></option>
                      {allSeller.map((item) => {
                        return (
                          <option key={item._id} value={item._id}>
                            {item.name}
                          </option>
                        );
                      })}
                    </Input>
                  </FormGroup>
                </Col>
                <Col xs="6" md="3">
                  <FormGroup>
                    <Label htmlFor="waranty">گارانتی</Label>
                    <Input
                      type="select"
                      name="waranty"
                      id="waranty"
                      value={waranty}
                      onChange={handleChangeWaranty}
                    >
                      <option></option>
                      {allWaranty.map((item) => {
                        return (
                          <option key={item._id} value={item._id}>
                            {item.name}
                          </option>
                        );
                      })}
                    </Input>
                  </FormGroup>
                </Col>
              </FormGroup>
              <Row xs="12">
                <Col xs="6" md="2">
                  <FormGroup>
                    <Label htmlFor="color">رنگ</Label>
                    <Input
                      type="select"
                      name="color"
                      id="color"
                      value={color}
                      onChange={handleChangeColor}
                    >
                      <option></option>
                      <option value="black">مشکی</option>
                      <option value="white">سفید</option>
                      <option value="red">قرمز</option>
                    </Input>
                  </FormGroup>
                </Col>
                <Col xs="6" md="3">
                  <FormGroup>
                    <Label htmlFor="stock">تعداد</Label>
                    <Input
                      type="number"
                      name="stock"
                      id="stock"
                      value={stock}
                      onChange={handleChangeAmount}
                    ></Input>
                  </FormGroup>
                </Col>
                <Col xs="6" md="3">
                  <FormGroup>
                    <Label htmlFor="price">قیمت</Label>
                    <Input
                      type="number"
                      name="price"
                      id="price"
                      value={price}
                      onChange={handleChangePrice}
                    ></Input>
                  </FormGroup>
                </Col>
                <Col xs="6" md="3">
                  <FormGroup>
                    <Label htmlFor="discontPrice">تخفیف</Label>
                    <Input
                      type="number"
                      name="discontPrice"
                      id="discontPrice"
                      value={discontPrice}
                      onChange={handleChangeDiscountPrice}
                    ></Input>
                  </FormGroup>
                </Col>
                <Col xs="6" md="1" className={Classes.addPlus}>
                  <span>
                    <Button color="success" onClick={addAttribute}>
                      <i className="fa fa-plus fa-lg"></i>
                    </Button>
                  </span>
                </Col>
              </Row>

              {allAttribute.map((item, index) => {
                const nameSeller = convertIdtoNameSeller(item.seller);
                const nameWaranty = convertIdtoNameWaranty(item.waranty);
                const color = item.color;
                return (
                  <Row key={index}>
                    <Col xs="6" md="2">
                      <FormGroup>
                        <Label htmlFor="valueSeller">فروشنده</Label>
                        <Input
                          type="text"
                          name="valueSeller"
                          id="valueSeller"
                          value={nameSeller}
                          disabled
                        />
                      </FormGroup>
                    </Col>
                    <Col xs="6" md="2">
                      <FormGroup>
                        <Label htmlFor="valueWaranty">گارانتی</Label>
                        <Input
                          type="text"
                          name="valueWaranty"
                          id="valueWaranty"
                          value={nameWaranty}
                          disabled
                        />
                      </FormGroup>
                    </Col>
                    <Col xs="4" md="1" className={Classes.color}>
                      <div
                        style={{
                          background: color,
                          width: "20px",
                          height: "20px",
                          borderRadius: "25px",
                        }}
                      ></div>
                    </Col>
                    <Col xs="6" md="2">
                      <FormGroup>
                        <Label htmlFor="valueStock">تعداد</Label>
                        <Input
                          type="number"
                          name="valueStock"
                          id="valueStock"
                          value={item.stock}
                          disabled
                        ></Input>
                      </FormGroup>
                    </Col>
                    <Col xs="6" md="2">
                      <FormGroup>
                        <Label htmlFor="valuePrice">قیمت</Label>
                        <Input
                          type="number"
                          name="valuePrice"
                          id="valuePrice"
                          value={item.price}
                          disabled
                        ></Input>
                      </FormGroup>
                    </Col>
                    <Col xs="6" md="2">
                      <FormGroup>
                        <Label htmlFor="valueDiscountPrice">تخفیف</Label>
                        <Input
                          type="number"
                          name="valueDiscountPrice"
                          id="valueDiscountPrice"
                          value={item.discount}
                          disabled
                        ></Input>
                      </FormGroup>
                    </Col>
                    <Col xs="6" md="1" className={Classes.addPlus}>
                      <span>
                        <Button
                          color="danger"
                          onClick={() => removeAttribute(index)}
                        >
                          <i className="fa fa-remove fa-lg"></i>
                        </Button>
                      </span>
                    </Col>
                  </Row>
                );
              })}

              {allSpecs.length > 0
                ? allSpecs.map((item, idx) => {
                    return (
                      <Row key={idx}>
                        <Col xs="12" md="12">
                          <Card>
                            <CardHeader>
                              <Label htmlFor="valueSeller">{item.name}</Label>
                            </CardHeader>
                            <CardBody>
                              {item.subSpec.map((item, index) => {
                                const IDValue = `name-${index}`;
                                const IDLabel = `label-${index}`;
                                return (
                                  <Row key={index}>
                                    <Col xs="4" md="4">
                                      <Input
                                        type="text"
                                        name="SubSpecs"
                                        id="SubSpecs"
                                        value={item.name}
                                        disabled
                                        className={Classes.inputSpec}
                                      />
                                    </Col>
                                    <Col xs="4" md="4">
                                      <Input
                                        type="text"
                                        name={IDValue}
                                        id={IDValue}
                                        placeholder="عنوان مقدار مشخصات را وارد نمائید"
                                        value={item.value}
                                        className={Classes.inputSpec}
                                        onChange={(e) =>
                                          handleValueSpecs(e, idx, index)
                                        }
                                      />
                                    </Col>
                                    <Col xs="4" md="4">
                                      <Input
                                        type="text"
                                        name={IDLabel}
                                        id={IDLabel}
                                        placeholder="توضیحات مقدار مشخصات را وارد نمائید"
                                        value={item.Label}
                                        className={Classes.inputSpec}
                                        onChange={(e) =>
                                          handleValueSpecsLabel(e, idx, index)
                                        }
                                      />
                                    </Col>
                                  </Row>
                                );
                              })}
                            </CardBody>
                          </Card>
                        </Col>
                      </Row>
                    );
                  })
                : null}
            </CardBody>

            <Row>
              <Col xs="12" dir="rtl">
                <FormGroup dir="rtl">
                  <label>توضیحات</label>
                  <CKEditor
                    editor={ClassicEditor}
                    className="cke_rtl"
                    data={description}
                    dir="rtl"
                    lan="fa"
                    onChange={(event, editor) =>
                      descriptionHandler(event, editor)
                    }
                    contentsLanguage="ar"
                  />
                </FormGroup>
              </Col>
            </Row>

            <Row className="mb-3 mr-3">
              <Col xs="6" className={Classes.centercontain}>
                <div className={Classes.addFile}>
                  <div className={Classes.addImage}>
                    <label htmlFor="add_media">گزینش تصویر</label>
                  </div>
                  <input
                    type="file"
                    id="add_media"
                    multiple
                    name="add_media"
                    onChange={handlerImage}
                  />
                </div>
              </Col>
              <Col xs="6">
                {image ? (
                  <img className={Classes.imageBrand} src={image} alt={image} />
                ) : null}
              </Col>
            </Row>

            <CardFooter>
              <Button
                type="submit"
                color="primary"
                size="sm"
                onClick={handleSubmitUplaod}
              >
                {SubmitLoading ? <Spinner size="sm" /> : "آپلود"}
              </Button>
            </CardFooter>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default Addproduct;
